import { saveAs } from 'file-saver';
import { createEvents } from 'ics';
import { formatDateForCalendar } from './dateUtils';

// ICS   
export const generateICSFile = (tasks, filename = 'graphics-roadmap-events.ics') => {
  const events = tasks.map(task => {
    const [year, month, day] = formatDateForCalendar(task.deadline).split('-').map(Number);
    
    return {
      title: `[${task.type === 'small' ? ' ' : task.type === 'weekly' ? ' ' : ' '}] ${task.title}`,
      description: `${task.phaseTitle}${task.weekTitle ? ' - ' + task.weekTitle : ''}`,
      start: [year, month, day],
      duration: { days: 1 },
      status: 'CONFIRMED',
      busyStatus: 'BUSY',
      categories: [' ', task.type]
    };
  });

  createEvents(events, (error, value) => {
    if (error) {
      console.error(error);
      return;
    }
    
    const blob = new Blob([value], { type: 'text/calendar;charset=utf-8' });
    saveAs(blob, filename);
  });
};

//    ICS  
export const generateSingleEventICS = (task) => {
  const taskName = task.title.replace(/[^a-zA-Z0-9-]/g, '-');
  generateICSFile([task], `${taskName}-.ics`);
};

//    ICS  
export const generateAllEventsICS = (phases) => {
  const allTasks = [];
  
  phases.forEach(phase => {
    phase.weeks.forEach(week => {
      if (week.smallTask) {
        allTasks.push({
          type: 'small',
          title: week.smallTask.title,
          deadline: week.smallTask.deadline,
          weekTitle: week.title,
          phaseTitle: phase.title
        });
      }
      
      if (week.weeklyTask) {
        allTasks.push({
          type: 'weekly',
          title: week.weeklyTask.title,
          deadline: week.weeklyTask.deadline,
          weekTitle: week.title,
          phaseTitle: phase.title
        });
      }
    });
    
    if (phase.monthlyProject) {
      allTasks.push({
        type: 'monthly',
        title: phase.monthlyProject.title,
        deadline: phase.monthlyProject.deadline,
        phaseTitle: phase.title
      });
    }
  });
  
  generateICSFile(allTasks, '--.ics');
};

//      ( )
export const findTasksForDate = (date, phases) => {
  if (!date) return [];
  
  const tasks = [];
  const dateString = `${date.getMonth() + 1}/${date.getDate()}`;
  
  phases.forEach(phase => {
    phase.weeks.forEach(week => {
      //    
      if (week.smallTask && week.smallTask.deadline.includes(dateString)) {
        tasks.push({
          type: 'small',
          title: week.smallTask.title,
          weekTitle: week.title,
          phaseTitle: phase.title,
          id: `small-${week.id}`,
          deadline: week.smallTask.deadline
        });
      }
      
      //    
      if (week.weeklyTask && week.weeklyTask.deadline.includes(dateString)) {
        tasks.push({
          type: 'weekly',
          title: week.weeklyTask.title,
          weekTitle: week.title,
          phaseTitle: phase.title,
          id: `weekly-${week.id}`,
          deadline: week.weeklyTask.deadline
        });
      }
    });
    
    //    
    if (phase.monthlyProject && phase.monthlyProject.deadline.includes(dateString)) {
      tasks.push({
        type: 'monthly',
        title: phase.monthlyProject.title,
        phaseTitle: phase.title,
        id: `monthly-${phase.id}`,
        deadline: phase.monthlyProject.deadline
      });
    }
  });
  
  return tasks;
};

//    ( )
export const generateCalendarData = () => {
  const months = [
    { year: 2025, month: 3, name: "2025 4", days: 30 },
    { year: 2025, month: 4, name: "2025 5", days: 31 },
    { year: 2025, month: 5, name: "2025 6", days: 30 },
    { year: 2025, month: 6, name: "2025 7", days: 31 },
    { year: 2025, month: 7, name: "2025 8", days: 31 },
    { year: 2025, month: 8, name: "2025 9", days: 30 },
    { year: 2025, month: 9, name: "2025 10", days: 31 }
  ];
  
  //     
  return months.map(month => {
    const firstDay = new Date(month.year, month.month, 1);
    const firstDayOfWeek = firstDay.getDay();
    
    const calendarDays = [];
    
    //     
    for (let i = 0; i < firstDayOfWeek; i++) {
      calendarDays.push(null);
    }
    
    //  
    for (let i = 1; i <= month.days; i++) {
      calendarDays.push(new Date(month.year, month.month, i));
    }
    
    return {
      ...month,
      calendarDays
    };
  });
};